/* eslint-disable react/prop-types, max-lines */
import React, { useMemo, useRef } from 'react';
import { DynamicSizeList } from '@john-osullivan/react-window-dynamic-fork';
import { getVirtualListComponent } from '@elliemae/ds-shared/virtualization';
import createInstancePlugin from '@elliemae/ds-shared/createDataInstance/createInstancePlugin';
import { TreeListNoItems } from '../../renderers';

// from react-window 1.8.5
function shallowDiffers(prev, next) {
  // eslint-disable-next-line no-restricted-syntax
  for (const attribute in prev) {
    if (!(attribute in next)) {
      return true;
    }
  }
  // eslint-disable-next-line no-restricted-syntax
  for (const attribute in next) {
    if (prev[attribute] !== next[attribute]) {
      return true;
    }
  }
  return false;
}

// more performant alternative to areEqual from react-window 1.8.5
function areEqualRecords(prevProps, nextProps) {
  const { style: prevStyle, index: pIdx, data: pData } = prevProps;
  const { style: nextStyle, index: nIdx, data: nData } = nextProps;

  return (
    !shallowDiffers(prevStyle, nextStyle) &&
    !shallowDiffers(pData?.records[pIdx], nData?.records[nIdx])
  );
}

// memoized list items strategy
// https://react-window.now.sh/#/examples/list/memoized-list-items
const VirtualizedTreeItem = React.memo(
  React.forwardRef(({ index, style, data }, ref) => {
    const { instance, records, listRef } = data;
    const { itemRenderer, getItemProps, renderers } = instance.getInstance();
    const record = records[index];

    const ItemComponent = renderers.item;

    // needed to show the shadow below the row and because
    // of absolute positioning from react-window
    const mixedProps = getItemProps(
      {
        index,
        record,
        style,
        listRef,
        innerRef: ref,
      },
      { record },
    );

    record.style = style;

    return (
      <ItemComponent {...mixedProps} tabIndex={-1}>
        {itemRenderer(mixedProps)}
      </ItemComponent>
    );
  }),
  areEqualRecords,
);

// eslint-disable-next-line react/display-name
const VirtualizedTree = (renderers, instance) => ({
  records,
  itemRenderer,
  innerRef,
  ...rest
}) => {
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const {
    props: {
      autoHeight,
      fluid,
      width,
      height = 500,
      noItemsPlaceholder = 'No items found',
      initialOffset,
    },
    state: { listRef },
    renderers: currRenderers,
    setListRef,
  } = instance.getInstance();

  // eslint-disable-next-line react-hooks/rules-of-hooks
  const ListComponent = useMemo(
    () =>
      getVirtualListComponent({
        component: DynamicSizeList,
        height,
        fluid,
        autoHeight,
      }),
    [height, autoHeight],
  );

  // eslint-disable-next-line react-hooks/rules-of-hooks
  const itemData = useMemo(
    () => ({
      instance,
      listRef,
      renderers: currRenderers,
      records,
    }),
    [records, currRenderers, listRef],
  ); // todo: revise this

  if (records && !records.length) {
    return <TreeListNoItems>{noItemsPlaceholder}</TreeListNoItems>;
  }

  return (
    <ListComponent
      innerRef={setListRef}
      {...rest}
      ref={listRef}
      component={DynamicSizeList}
      height={height}
      initialScrollOffset={initialOffset}
      itemCount={records.length}
      itemData={itemData}
      outerElementType={renderers.list}
      outerRef={innerRef}
      width={width}
    >
      {VirtualizedTreeItem}
    </ListComponent>
  );
};

export const TreeVirtualizationPlugin = createInstancePlugin(
  'tree-virtualization',
  {
    state() {
      // eslint-disable-next-line react-hooks/rules-of-hooks
      const listRef = useRef();
      return {
        state: { listRef },
        actions: {
          scrollTo: (...args) => {
            if (listRef?.current?.scrollTo) {
              listRef.current.scrollTo(args);
            }
          },
          scrollToItem: (...args) => {
            if (listRef?.current?.scrollToItem) {
              listRef.current.scrollToItem(args);
            }
          },
        },
      };
    },
    renderers(renderers, instance) {
      return {
        item: renderers.item,
        // eslint-disable-next-line react-hooks/rules-of-hooks
        list: useMemo(() => VirtualizedTree(renderers, instance), []),
      };
    },
  },
);
